.MatchDetailCard {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: .7rem;
    margin-bottom: 1rem;
    .MatchDate, .MatchVenu, .MatchResult {
        margin-top: .5rem;
    }

    .AdditionalDetail {
        text-align: right;
        & > p {
            margin-bottom: 1rem;
        }
    }
}