@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;600&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: #fff
}

body {
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #282c34;
  color: #fff;
  max-width: 1200px;
  margin: 0 auto;
}
.App{padding:1rem;text-align:left}.Win{background:#77d18c}.Loss{background:#d17a77}

.Teams{list-style:none;display:grid;grid-template-columns:1fr 1fr;grid-column-gap:1rem;grid-row-gap:1rem}.Teams .Team{font-size:2rem;font-weight:bold;border:1px solid #ccc;padding:3rem 2rem}.Teams .Team:hover{background:#fff}.Teams .Team:hover>a{color:#282c34}

.HomePage{margin-top:2rem}.HomePage>h1{margin-bottom:1rem;font-size:3rem}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:.7rem;margin-bottom:1rem}.MatchDetailCard .MatchDate,.MatchDetailCard .MatchVenu,.MatchDetailCard .MatchResult{margin-top:.5rem}.MatchDetailCard .AdditionalDetail{text-align:right}.MatchDetailCard .AdditionalDetail>p{margin-bottom:1rem}

.YearSelector{list-style:none}.YearSelector li{margin:0.5rem 0}

.MatchPage{margin-top:2rem;display:grid;grid-template-columns:1fr 5fr;grid-column-gap:2rem;grid-row-gap:1rem}.MatchPage .YearSelector-Section{justify-self:end;text-align:right}.MatchPage .Matches-Heading{margin-bottom:1rem}

.MatchSmallCard{padding:.7rem;margin-bottom:2rem}.MatchSmallCard .MatchWinner{margin-top:.5rem}

.WinVsLossPieChart{padding:2rem}.WinVsLossPieChart .WinLoss-Title{text-align:center;margin-bottom:1rem}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:1rem;grid-row-gap:1rem}.TeamPage .TeamName-Section{grid-column:span 3;justify-self:start;align-self:end;margin-bottom:1rem}.TeamPage .TeamName-Section .TeamName{font-size:4rem}.TeamPage .Win-Loss-Section{justify-self:center}.TeamPage .MatchDetailCard-Section{grid-column:span 4}.TeamPage .More-Section{justify-self:center;-webkit-align-self:center;align-self:center;font-size:1.5rem}

