.MatchPage {
    margin-top: 2rem;
    display: grid;
    grid-template-columns: 1fr 5fr;
    grid-column-gap: 2rem;
    grid-row-gap: 1rem;
    
    .YearSelector-Section {
        justify-self: end;
        text-align: right;
    }

    .Matches-Heading {
        margin-bottom: 1rem;
    }
}