.Teams {
    list-style: none;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 1rem;
    grid-row-gap: 1rem;

    .Team {
        font-size: 2rem;
        font-weight: bold;
        border: 1px solid #ccc;
        padding: 3rem 2rem;
        &:hover {
            background: #fff;
            & > a {
                color: #282c34;
            }
        }
    }
}