.TeamPage {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-column-gap: 1rem;
    grid-row-gap: 1rem;

    .TeamName-Section {
        grid-column: span 3;
        justify-self: start;
        align-self: end;
        margin-bottom: 1rem;
        .TeamName {
            font-size: 4rem;
        }
    }

    .Win-Loss-Section {
        justify-self: center;    
    }

    .MatchDetailCard-Section {
        grid-column: span 4;
    }

    .More-Section {
        justify-self: center;
        align-self: center;
        font-size: 1.5rem;    
    }
}